  transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  -o-transform: rotate(45deg);
}
.messageboard-list ul {
  list-style-type: none;
  height: 100%;
  overflow-y: hidden;
}
.messageboard-list ul:hover {
  overflow-y: auto;
}
.messageboard-list ul li{
  position: relative;
  margin-bottom: 20px;
  cursor: pointer;
}
.messageboard-list .messageboard-name {
  margin-right: 120px;
  overflow: hidden;
}
.messageboard-list .messageboard-date {
  position: absolute;
  top: 0;right: 0;
  width: 120px;
  text-align: center;
  background: #124149;
  overflow: hidden;
}
.messageboard-list .messageboard-content{
  overflow: hidden;
}

.home-avatar {
  position: absolute;
  padding: 0 10px;
  margin-bottom: 50px;
  bottom: 0;
  right: 20px;
  width: 100%;
  height: 100px;
  max-width: 300px;
  border-left: 1px solid;
  border-right: 1px solid;
  cursor: pointer;
}
.home-avatar .top {
  position: absolute;
  top: -5px;
  left: -2px;
  width: 12px;
  height: 1px;
  background: #87B9CB;
}
.home-avatar .bottom {
  position: absolute;
  bottom: -5px;
  left: -2px;
  width: 12px;
  height: 1px;
  background: #87B9CB;
}
.home-avatar .left {
  transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
}
.home-avatar .right {
  left: auto;
  right: -2px;
  transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  -o-transform: rotate(45deg);
}
.home-avatar .bottom.left {
  transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  -o-transform: rotate(45deg);
}
.home-avatar .bottom.right {
  transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
}
.home-avatar .avatar-image {
  float: left;
  width: 100px;
  height: 100%;
  border: 1px solid;
  background-image: url(/static/img/avatar.c580a9b.jpg);
  background-size: 100% 100%;
}
.home-avatar .avatar-info {
  margin-left: 100px;
  padding-left: 10px;
}
.home-avatar .avatar-info-name {
  margin-bottom: 10px;
  margin-top: 10px;
}
.home-avatar .avatar-info-name > span {
  color: #f46135
}
.home-avatar .avatar-info-hp,
.home-avatar .avatar-info-mp {
  height: 30px;
  line-height: 30px;
  font-size: 16px;
}
.home-avatar .avatar-info-hp > span,
.home-avatar .avatar-info-mp > span{
  float: left;
  width: 30px;
}
.avatar-info .avatar-info-grid {
  margin-left: 30px;
  padding: 3px 0;
}
.avatar-info .avatar-info-grid .avatar-info-grid-block {
  margin: 2px;
  display: inline-block;
  width: 7%;
  height: 15px;
  border: 1px solid;
}
.avatar-info .avatar-info-grid .avatar-info-grid-block.block-on {
  background: #87B9CB;
}























